/* eslint-disable react/jsx-key */
import React, { lazy } from 'react'
import AuthorizedRoute from 'base-shell/lib/components/AuthorizedRoute/AuthorizedRoute'
import UnauthorizedRoute from 'base-shell/lib/components/UnauthorizedRoute/UnauthorizedRoute'
import { Route } from 'react-router-dom'
import PersonalPage from 'pages/PersonalPage'
import EditAccount from 'pages/PersonalPage/EditAccount'
import CropAvatar from 'pages/PersonalPage/CropAvatar'

import TrailCard from 'components/Lists/TrailCard'
import CommentPage from 'pages/Comment/CommentPage'
import TestSwiper from 'pages/playground/test'


// const SignIn = lazy(() => import('../pages/SignIn/SignIn'))
const SignUp = lazy(() => import('../pages/SignUp/SignUp'))
const PasswordReset = lazy(() => import('../pages/PasswordReset/PasswordReset'))
const About = lazy(() => import('../pages/About'))
const Home = lazy(() => import('../pages/Home/Home'))
const DialogDemo = lazy(() => import('../pages/DialogDemo/DialogDemo'))
const ToastDemo = lazy(() => import('../pages/ToastDemo/ToastDemo'))
const FilterDemo = lazy(() => import('../pages/FilterDemo'))
const ListPageDemo = lazy(() => import('../pages/ListPageDemo'))
const TabsDemo = lazy(() => import('../pages/TabsDemo'))
const MyAccount = lazy(() => import('../pages/MyAccount/MyAccount'))

const SignIn = lazy(() => import('../pages/HikingFrontend/SignIn'))
const Verify2 = lazy(() => import('../pages/HikingFrontend/Verify2'))

const SearchPage = lazy(() => import('../pages/SearchPage/SearchPage'))
const SearchQuick = lazy(() => import('../pages/SearchQuick/SearchQuick'))
const SearchResult = lazy(() => import('../pages/SearchResult/SearchResult'))
const CollectPage = lazy(() => import('../pages/CollectPage/CollectPage'))
const CollectPage2 = lazy(() => import('../pages/CollectPage/CollectPage2'))
const TrailComment = lazy(() => import('../pages/TrailComment/TrailComment'))
const TrailComment2 = lazy(() => import('../pages/TrailComment/TrailComment2'))
const PrivacyPolicy = lazy(()=> import('../pages/PrivacyPolicy/PrivacyPolicy'))
const AboutUs =lazy(()=>import('../pages/AboutUs/AboutUs'))
const ColumnPage = lazy(() => import('../pages/ColumnPage/ColumnPage'))

const Login1 = lazy(() => import('../pages/HikingFrontend/Login1'))
const Login2 = lazy(() => import('../pages/HikingFrontend/Login2'))
const Register0_1 = lazy(() => import('../pages/HikingFrontend/Register0_1'))
const ResetPassword = lazy(() => import('../pages/HikingFrontend/ResetPassword'))
const NearByPathway = lazy(()=>import('../pages/HikingFrontend/NearByPathway'));
const Attraction = lazy(()=>import('../pages/HikingFrontend/Attraction'));
const Pathway = lazy(()=>import('../pages/HikingFrontend/Pathway'));
const Announcement = lazy(()=>import('../pages/HikingFrontend/Announcement'));
const Trailhead = lazy(()=>import('../pages/HikingFrontend/Trailhead'));


const routes = [
  //Old one Routes  >> <Route path="/signin" redirectTo="/" exact component={SignIn} />,
  <Route path="/signin" redirectTo="/" exact component={props => <SignIn {...props} />} />,
  <Route path="/signup" redirectTo="/" exact component={props => <SignUp {...props} />} />,
  <Route path="/verify2" redirectTo="/" exact component={props => <Verify2 {...props} />} />,
  <Route path="/login1" redirectTo="/" exact component={props => <Login1 {...props} />} />,
  <Route path="/login2" redirectTo="/" exact component={props => <Login2 {...props} />} />,
  <Route path="/register0_1" redirectTo="/" exact component={props => <Register0_1 {...props} />} />,
  <Route path="/resetPassword" redirectTo="/" exact component={props => <ResetPassword {...props} />} />,
  <Route path="/password_reset" redirectTo="/" exact component={props => <PasswordReset {...props} />} />,

  <Route path="/trailComment" exact component={props => <TrailComment {...props} />} />,
  <Route path="/trailComment2" exact component={props => <TrailComment2 {...props} />} />,
  <Route path="/searchPage" exact component={props => <SearchPage {...props} />} />,
  <Route path="/searchQuick/:id" exact component={props => <SearchQuick {...props} />} />,
  <Route path="/searchResult" exact component={props => <SearchResult {...props} />} />,
  <Route path="/personalPage" exact component={props => <PersonalPage {...props} />} />,
  <Route path="/editAccount" exact component={props => <EditAccount {...props} />} />,
  <Route path="/cropAvatar" exact component={props => <CropAvatar {...props} />} />,
  <Route path="/privacyPolicy" exact component={props => <PrivacyPolicy {...props} />} />,
  <Route path="/aboutUs" exact component={props => <AboutUs {...props} />} />,
  <Route path="/Home" exact component={props => <Home {...props} />} />,
  <Route path="/about" exact component={props => <About {...props} />} />,
  <Route path="/collectPage" exact component={props => <CollectPage {...props} />} />,
  <Route path="/collectPage2" exact component={props => <CollectPage2 {...props} />} />,
  <Route path="/columnPage/:id" exact component={props => <ColumnPage {...props} />} />,
  <Route path="/NearByPathway" exact component={props => <NearByPathway {...props} />} />,
  <Route path="/attraction" exact component={props => <Attraction {...props} />} />,

  <Route path="/pathway" exact component={props => <Pathway {...props} />} />,
  <Route path="/announcement" exact component={props => <Announcement {...props} />} />,
  <Route path="/trailhead" exact component={props => <Trailhead {...props} />} />,
  <Route path="/commentPage" exact component={props => <CommentPage {...props} />} />,
  <Route path="/test" exact component={props => <TestSwiper {...props} />} />,

]

export default routes
